'use client';

import { useEffect, useRef, useState, useCallback } from 'react';
import { US_STATES } from '@/types/agency';

interface InteractiveUSMapProps {
  onStateClick?: (stateCode: string) => void;
  selectedState?: string | null;
}

declare global {
  interface Window {
    jQuery: any;
    $: any;
    Raphael: any;
  }
}

export default function InteractiveUSMap({ onStateClick, selectedState }: InteractiveUSMapProps) {
  const mapContainerRef = useRef<HTMLDivElement>(null);
  const [scriptsLoaded, setScriptsLoaded] = useState(false);
  const [hoveredState, setHoveredState] = useState<string | null>(null);
  const mapInitializedRef = useRef(false);

  // Load external scripts (jQuery, Raphael, usmap)
  useEffect(() => {
    const loadScript = (src: string): Promise<void> => {
      return new Promise((resolve, reject) => {
        // Check if script already exists
        if (document.querySelector(`script[src="${src}"]`)) {
          resolve();
          return;
        }

        const script = document.createElement('script');
        script.src = src;
        script.async = true;
        script.onload = () => resolve();
        script.onerror = () => reject(new Error(`Failed to load script: ${src}`));
        document.body.appendChild(script);
      });
    };

    const loadAllScripts = async () => {
      try {
        // Load in order: jQuery -> Raphael -> usmap
        await loadScript('https://code.jquery.com/jquery-3.7.1.min.js');
        await loadScript('/raphael.js');
        await loadScript('/jquery.usmap.js');
        
        setScriptsLoaded(true);
      } catch (error) {
        console.error('Error loading map scripts:', error);
      }
    };

    loadAllScripts();
  }, []);

  // Initialize the map once scripts are loaded
  useEffect(() => {
    if (!scriptsLoaded || !mapContainerRef.current || !window.jQuery || mapInitializedRef.current) return;

    const $ = window.jQuery;
    const Raphael = window.Raphael;

    // Initialize the usmap plugin
    $(mapContainerRef.current).usmap({
      // Default state styles
      stateStyles: {
        fill: '#1e3a8a', // Dark blue
        stroke: '#ffffff',
        'stroke-width': 1.5,
        'stroke-linejoin': 'round'
      },

      // Hover styles - states turn BLACK on hover
      stateHoverStyles: {
        fill: '#000000',
        stroke: '#ffffff',
        'stroke-width': 2
      },

      // Smooth animation
      stateHoverAnimation: 200,

      // Show labels for small northeastern states
      showLabels: true,

      labelBackingStyles: {
        fill: '#1e3a8a',
        stroke: '#ffffff',
        'stroke-width': 1
      },

      labelBackingHoverStyles: {
        fill: '#000000',
        stroke: '#ffffff'
      },

      labelTextStyles: {
        fill: '#ffffff',
        'font-size': '10px',
        'font-weight': '600'
      },

      // Mouse over handler
      mouseover: function (event: any, data: any) {
        const stateCode = data.name;
        setHoveredState(stateCode);
      },

      // Mouse out handler
      mouseout: function (event: any, data: any) {
        setHoveredState(null);
      },

      // Click handler
      click: function (event: any, data: any) {
        const stateCode = data.name;
        if (onStateClick) {
          onStateClick(stateCode);
        }
      }
    });

    // Add custom state labels for larger states (excluding small east coast states, AK, HI)
    // Small east coast states already have labels: VT, NH, MA, RI, CT, NJ, DE, MD, DC
    // Use setTimeout to ensure the map is fully initialized
    setTimeout(() => {
      if (!mapContainerRef.current) return;
      
      const mapData = $(mapContainerRef.current).data('usmap');
      if (!mapData || !mapData.paper || !mapData.stateShapes) {
        console.log('Map data not ready yet');
        return;
      }

      const statesToLabel = [
        'WA', 'OR', 'CA', 'NV', 'ID', 'MT', 'WY', 'UT', 'AZ', 'NM', 'CO',
        'ND', 'SD', 'NE', 'KS', 'OK', 'TX', 'MN', 'IA', 'MO', 'AR', 'LA',
        'WI', 'IL', 'MS', 'MI', 'IN', 'OH', 'KY', 'TN', 'AL', 'WV', 'VA',
        'NC', 'SC', 'GA', 'FL', 'PA', 'NY', 'ME'
      ];

      const paper = mapData.paper;
      const stateShapes = mapData.stateShapes;

      statesToLabel.forEach((state) => {
        if (stateShapes[state]) {
          try {
            const bbox = stateShapes[state].getBBox();
            const centerX = bbox.x + bbox.width / 2;
            const centerY = bbox.y + bbox.height / 2;

            // Add text label
            paper.text(centerX, centerY, state).attr({
              'fill': '#ffffff',
              'font-size': '11px',
              'font-weight': 'bold',
              'opacity': 0.8,
              'pointer-events': 'none'
            });
          } catch (err) {
            console.error(`Error adding label for ${state}:`, err);
          }
        }
      });
    }, 200);

    mapInitializedRef.current = true;

    // Cleanup only on unmount
    return () => {
      if (mapContainerRef.current && mapInitializedRef.current) {
        $(mapContainerRef.current).empty();
        mapInitializedRef.current = false;
      }
    };
  }, [scriptsLoaded]);

  const displayState = hoveredState || selectedState;
  const stateName = displayState ? US_STATES[displayState] : null;

  return (
    <div className="w-full max-w-7xl mx-auto">
      {/* Cinematic Spy-Tech Dashboard Layout */}
      <div className="grid grid-cols-1 lg:grid-cols-[2fr,1.2fr] gap-6 items-start">
        
        {/* Map Container - High-Tech Command Center */}
        <div 
          className="relative rounded-lg border overflow-hidden" 
          style={{ 
            height: '580px',
            background: 'linear-gradient(165deg, #0a0e1a 0%, #000000 100%)',
            borderColor: 'rgba(100, 116, 139, 0.15)',
            boxShadow: '0 8px 32px rgba(0, 0, 0, 0.4), inset 0 1px 0 rgba(148, 163, 184, 0.05)'
          }}
        >
          {/* Subtle corner accents - spy tech detail */}
          <div className="absolute top-0 left-0 w-16 h-16 border-l-2 border-t-2 opacity-20" style={{ borderColor: '#60a5fa' }}></div>
          <div className="absolute top-0 right-0 w-16 h-16 border-r-2 border-t-2 opacity-20" style={{ borderColor: '#60a5fa' }}></div>
          <div className="absolute bottom-0 left-0 w-16 h-16 border-l-2 border-b-2 opacity-20" style={{ borderColor: '#60a5fa' }}></div>
          <div className="absolute bottom-0 right-0 w-16 h-16 border-r-2 border-b-2 opacity-20" style={{ borderColor: '#60a5fa' }}></div>
          
          <div 
            ref={mapContainerRef} 
            className="w-full h-full p-8"
          />

          {/* Loading Overlay - High-tech style */}
          {!scriptsLoaded && (
            <div className="absolute inset-0 flex items-center justify-center" style={{ background: 'linear-gradient(165deg, #0a0e1a 0%, #000000 100%)' }}>
              <div className="text-center">
                <div className="relative w-16 h-16 mx-auto mb-4">
                  <div className="absolute inset-0 border-2 border-blue-500/30 rounded-full"></div>
                  <div className="absolute inset-0 border-2 border-blue-400 border-t-transparent rounded-full animate-spin"></div>
                </div>
                <p className="text-slate-300 font-light text-sm tracking-wider">INITIALIZING SYSTEM</p>
              </div>
            </div>
          )}
        </div>

        {/* Info Panel - Mission Control Interface */}
        <div 
          className="rounded-lg border flex flex-col" 
          style={{ 
            height: '580px',
            background: 'linear-gradient(165deg, #0a0e1a 0%, #000000 100%)',
            borderColor: 'rgba(100, 116, 139, 0.15)',
            boxShadow: '0 8px 32px rgba(0, 0, 0, 0.4), inset 0 1px 0 rgba(148, 163, 184, 0.05)'
          }}
        >
          {/* Header with status indicator */}
          <div className="p-6 border-b" style={{ borderColor: 'rgba(100, 116, 139, 0.1)' }}>
            <div className="flex items-center justify-between mb-3">
              <h1 className="text-sm font-light text-slate-400 tracking-[0.2em] uppercase">System Status</h1>
              <div className="flex items-center gap-2">
                <div className="w-1.5 h-1.5 rounded-full bg-emerald-400 animate-pulse"></div>
                <span className="text-xs text-emerald-400 font-light tracking-wider">OPERATIONAL</span>
              </div>
            </div>
            <h2 className="text-xl font-light text-slate-200 tracking-wide">
              Agency Locator
            </h2>
          </div>

          {/* Target Display - Spy tech readout */}
          <div className="px-6 py-8 border-b" style={{ borderColor: 'rgba(100, 116, 139, 0.1)' }}>
            <div className="text-xs font-light text-slate-500 tracking-[0.15em] uppercase mb-3">
              Target Region
            </div>
            
            {displayState ? (
              <div>
                <div className="text-3xl font-light text-slate-100 mb-2 tracking-tight">
                  {stateName}
                </div>
                <div className="flex items-center gap-2">
                  <div className="px-2 py-1 rounded text-xs font-mono tracking-wider" style={{ 
                    background: 'rgba(59, 130, 246, 0.1)',
                    color: '#93c5fd',
                    border: '1px solid rgba(59, 130, 246, 0.2)'
                  }}>
                    {displayState}
                  </div>
                  {hoveredState && (
                    <div className="text-xs text-slate-500 font-light tracking-wide animate-pulse">
                      › Click to engage
                    </div>
                  )}
                </div>
              </div>
            ) : (
              <div className="text-slate-600 font-light text-sm tracking-wide">
                Awaiting target selection...
              </div>
            )}
          </div>

          {/* Intelligence Readout - Clean data display */}
          <div className="px-6 py-6 flex-1">
            <div className="text-xs font-light text-slate-500 tracking-[0.15em] uppercase mb-4">
              Intelligence
            </div>
            <div className="space-y-4">
              <div className="flex justify-between items-baseline py-2 border-b border-slate-800/50">
                <span className="text-sm text-slate-400 font-light">Agencies Located</span>
                <span className="text-lg text-slate-300 font-light tabular-nums">—</span>
              </div>
              <div className="flex justify-between items-baseline py-2 border-b border-slate-800/50">
                <span className="text-sm text-slate-400 font-light">Certification Status</span>
                <span className="text-lg text-slate-300 font-light tabular-nums">—</span>
              </div>
              <div className="flex justify-between items-baseline py-2 border-b border-slate-800/50">
                <span className="text-sm text-slate-400 font-light">Quality Index</span>
                <span className="text-lg text-slate-300 font-light tabular-nums">—</span>
              </div>
            </div>
          </div>

          {/* Footer note - subtle and elegant */}
          <div className="px-6 py-4 border-t" style={{ borderColor: 'rgba(100, 116, 139, 0.1)' }}>
            <p className="text-xs text-slate-600 font-light leading-relaxed">
              Hover over regions to preview. Click to lock target and view detailed intelligence.
            </p>
          </div>
        </div>

      </div>
    </div>
  );
}
