'use client';

import { useState } from 'react';
import InteractiveUSMap from '@/components/InteractiveUSMap';
import AgencyTable from '@/components/AgencyTable';
import { StateAgencies, US_STATES } from '@/types/agency';

// Mock data for demonstration - will be replaced with real data from API
const mockStateAgencies: StateAgencies[] = Object.keys(US_STATES).map((state) => ({
  state,
  stateName: US_STATES[state],
  count: 0,
  agencies: [],
}));

export default function HomePage() {
  const [selectedState, setSelectedState] = useState<string | null>(null);

  // Handle state click with smooth scroll
  const handleStateClick = (stateCode: string) => {
    setSelectedState(stateCode);
    
    // Smooth scroll to the state section
    const element = document.getElementById(`state-${stateCode}`);
    if (element) {
      element.scrollIntoView({ 
        behavior: 'smooth', 
        block: 'start' 
      });
    }
  };

  return (
    <main className="min-h-screen bg-black">
      {/* Header - Sleek & Minimal */}
      <header className="bg-gray-950 text-white py-8 shadow-lg border-b border-gray-800">
        <div className="container mx-auto px-4">
          <h1 className="text-4xl font-bold text-center text-white mb-3 tracking-tight">
            Medicare Agency Finder
          </h1>
          <div className="flex items-center justify-center gap-2">
            <span className="w-1.5 h-1.5 bg-green-400 rounded-full animate-pulse"></span>
            <p className="text-xs text-gray-400 font-medium uppercase tracking-wide">
              Data sourced from CMS.gov
            </p>
          </div>
        </div>
      </header>

      {/* US Map Section - Dashboard Style */}
      <section className="py-16 bg-black">
        <div className="container mx-auto px-4">
          <InteractiveUSMap 
            onStateClick={handleStateClick}
            selectedState={selectedState}
          />
        </div>
      </section>

      {/* State Sections */}
      <section className="py-12 bg-black">
        <div className="container mx-auto px-4 max-w-7xl">
          {mockStateAgencies.map((stateData) => (
            <div
              key={stateData.state}
              id={`state-${stateData.state}`}
              className="mb-8 scroll-mt-4"
            >
              <div className="bg-gradient-to-br from-gray-800 to-gray-900 rounded-2xl shadow-2xl border border-gray-700 p-8 hover:border-gray-600 transition-all duration-300">
                <div className="flex items-center justify-between mb-6">
                  <div>
                    <div className="text-sm text-gray-400 uppercase tracking-wider mb-1">
                      {stateData.state}
                    </div>
                    <h2 className="text-3xl font-bold text-white">
                      {stateData.stateName}
                    </h2>
                  </div>
                  <div className="text-right">
                    <div className="text-sm text-gray-400 uppercase tracking-wider mb-1">
                      Total Agencies
                    </div>
                    <div className="text-2xl font-bold text-blue-400">
                      {stateData.count}
                    </div>
                  </div>
                </div>
                
                <AgencyTable agencies={stateData.agencies} />
              </div>
            </div>
          ))}
        </div>
      </section>

      {/* Footer */}
      <footer className="bg-gray-950 text-white py-8 mt-12 border-t border-gray-800">
        <div className="container mx-auto px-4 text-center">
          <p className="text-sm text-gray-400">
            &copy; {new Date().getFullYear()} Medicare Agency Finder. 
            Data provided by{' '}
            <a
              href="https://data.cms.gov/provider-data/dataset/6jpm-sxkc"
              target="_blank"
              rel="noopener noreferrer"
              className="text-blue-400 hover:text-blue-300 underline transition-colors"
            >
              CMS.gov
            </a>
          </p>
        </div>
      </footer>
    </main>
  );
}
